import React from 'react';
import {
  Chart as ChartJS,
  LineElement,
  PointElement,
  CategoryScale,
  LinearScale,
  Tooltip,
  Legend,
  Title,
  Filler
} from 'chart.js';
import zoomPlugin from 'chartjs-plugin-zoom';
import { Line } from 'react-chartjs-2';

ChartJS.register(LineElement, PointElement, CategoryScale, LinearScale, Tooltip, Legend, Title, Filler, zoomPlugin);

interface ILineChartData {
  month: string;  // : '2021'
  count: number;  //  
}

// Props registrationYear 
interface Props {
  chartData: ILineChartData[];
  fromLabel?: string; //  
  registrationYear: number; //   
}

const AnnualLineChart: React.FC<Props> = ({ chartData, fromLabel, registrationYear }) => {
  //   
  const currentYear = new Date().getFullYear();
  let startYear: number, endYear: number;
  if (registrationYear === currentYear) {
    startYear = currentYear;
    endYear = currentYear + 5;
  } else {
    startYear = registrationYear;
    endYear = currentYear;
  }
  const totalYears = endYear - startYear + 1;
  // labels  full range   (: '2020', '2021', ...)
  const fullLabels = [];
  for (let y = startYear; y <= endYear; y++) {
    fullLabels.push(`${y}`);
  }
  
  //  chartData  fullLabels    
  //  zoom window:  6 ,   6    
  let initialMinIndex = 0, initialMaxIndex = fullLabels.length - 1;
  if (fullLabels.length > 6) {
    initialMinIndex = fullLabels.length - 6;
    initialMaxIndex = fullLabels.length - 1;
  }
  
  // x  fullLabels  ( chartData     )
  const labels = fullLabels;
  
  //  : chartData  count   
  const data = {
    labels,
    datasets: [
      {
        label: ' ',
        data: chartData.map(item => item.count),
        borderColor: '#FF7849',
        backgroundColor: 'rgba(255,120,73,0.1)',
        pointBackgroundColor: '#FF7849',
        fill: true,
        tension: 0.3,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: { display: false },
      title: { display: false },
      zoom: {
        pan: {
          enabled: true,
          mode: 'x' as const,
        },
        zoom: {
          wheel: { enabled: false },
          pinch: { enabled: false },
          drag: { enabled: false },
          mode: 'x' as const,
        },
      },
    },
    scales: {
      x: {
        grid: { color: '#f5f5f5' },
        ticks: {
          // x  :      
          color: (context: any) => {
            if (context.tick && context.tick.label) {
              const label = context.tick.label;
              if (label === `${currentYear}`) {
                return 'red';
              }
            }
            return '#666';
          },
        },
        min: initialMinIndex,
        max: initialMaxIndex,
      },
      y: {
        display: false,
      },
    },
  };

  return (
    <div style={{ width: '100%', height: '70%', marginTop: '10px' }}>
      {fromLabel && (
        <div style={{ textAlign: 'right', fontSize: '14px', color: '#999', marginBottom: '10px' }}>
          {fromLabel}
        </div>
      )}
      {chartData.length > 0 ? (
        <div style={{ position: 'relative', width: '100%', height: '100%' }}>
          <Line data={data} options={options} />
        </div>
      ) : (
        <div
          style={{
            height: '100%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#999',
            border: '1px dashed #ccc',
            borderRadius: '8px',
          }}
        >
           .
        </div>
      )}
    </div>
  );
};

export default AnnualLineChart;
