import React, { useState, useRef, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
const BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8083';

interface Memo {
  id: string;
  pageNumber: string;
  memo: string;
  isMemoSaved: boolean;
  
}

const CreatePostPage: React.FC = () => {
  const navigate = useNavigate();
  const { id: postId } = useParams(); // URL  id (  )

  //   
  const [showToast, setShowToast] = useState(false);
  const [showConfirmBackModal, setShowConfirmBackModal] = useState(false);
  const [showCongratsModal, setShowCongratsModal] = useState(false);
  const [isSaved, setIsSaved] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [readingStatus, setReadingStatus] = useState<'' | ''>('');

  const [title, setTitle] = useState('');
  const [titleError, setTitleError] = useState(false);
  const titleInputRef = useRef<HTMLInputElement>(null);

  const [startDateError, setStartDateError] = useState(false);
  const [inputAuthorError, setInputAuthorError] = useState(false);
  const [genreError, setGenreError] = useState(false);
  const startDateRef = useRef<HTMLInputElement>(null);
  const inputAuthorRef = useRef<HTMLInputElement>(null);
  const genreRef = useRef<HTMLSelectElement>(null);


  const [memos, setMemos] = useState<Memo[]>([
    { id: String(Date.now()), pageNumber: '', memo: '', isMemoSaved: false },
  ]);
  const [savedPostId, setSavedPostId] = useState<string | null>(null);

  const [hasShownCongrats, setHasShownCongrats] = useState(false);

  const [customGenre, setCustomGenre] = useState('');

  const textAreaRefs = useRef<{ [key: string]: HTMLTextAreaElement | null }>({});

  const [isDirectGenre, setIsDirectGenre] = useState(false);

  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [inputAuthor, setInputAuthor] = useState('');
  const [genre, setGenre] = useState('');
  const [publisher, setPublisher] = useState('');
  const [summary, setSummary] = useState('');
  const [review, setReview] = useState('');

  // :   ( ) 
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [_uploadedImage, setUploadedImage] = useState<File | null>(null);
  const [backgroundImage, setBackgroundImage] = useState('');

  // :      state 
  const [showDraftModal, setShowDraftModal] = useState(false);
  const [draftData, setDraftData] = useState<any>(null);

  //    localStorage      
  useEffect(() => {
    const draft = localStorage.getItem('createPostDraft');
    if (draft) {
      setDraftData(JSON.parse(draft));
      setShowDraftModal(true);
    }
  }, []);


  // (1)   :   
  useEffect(() => {
    if (!postId) return; //   postId 
    fetch(`${BASE_URL}/posts/${postId}`)
      .then((res) => {
        if (!res.ok) throw new Error('  ');
        return res.json();
      })
      .then((data) => {
        setTitle(data.title);
        setStartDate(data.startDate);
        setReadingStatus(data.readingStatus);
        setInputAuthor(data.inputAuthor);
        setGenre(data.genre);
        setPublisher(data.publisher);
        setSummary(data.summary);
        setReview(data.review);
        setEndDate(data.endDate);
        if (data.endDate) {
          setHasShownCongrats(true);
        }

        // memos
        if (data.memos) {
          setMemos(
            data.memos.map((m: any, index: number) => ({
              id: m.id ? String(m.id) : `${Date.now()}-${index}`,
              pageNumber: m.pageNumber,
              memo: m.memo,
              isMemoSaved: true,
            }))
          );
        }
        if (data.titleBackgroundImage) {
          setBackgroundImage(data.titleBackgroundImage);
        }
      })
      .catch((err) => console.error('Error fetching post:', err));
  }, [postId]);

  // (2)  
  const handleGoBack = () => {
    if (isSaved) {
      navigate('/booknote');
    } else {
      setShowConfirmBackModal(true);
    }
  };
  const confirmGoBack = () => {
    setShowConfirmBackModal(false);
    handleSave(undefined, true, false);
    //navigate('/booknote');
  };
  const cancelGoBack = () => {
    setShowConfirmBackModal(false);
  };

  // (3)     
  const handleCloseBookDate = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newEndDate = e.target.value;
    if (newEndDate) {
      setReadingStatus('');
      if (!hasShownCongrats) {
        setShowCongratsModal(true);
        setHasShownCongrats(true);
      }
    } else {
      setReadingStatus('');
    }
  };

  const handleCloseCongratsModal = () => {
    setShowCongratsModal(false);
  };

  // (4)  
  const autoResize = (element: HTMLTextAreaElement) => {
    element.style.height = 'auto';
    element.style.height = element.scrollHeight + 'px';
  };
  const handleAddMemo = () => {
    const newMemo: Memo = {
      id: String(Date.now()),
      pageNumber: '',
      memo: '',
      isMemoSaved: false,
    };
    setMemos([...memos, newMemo]);
  };
  //  
  const handleRegisterMemo = (id: string) => {
    setMemos((prev) =>
      prev.map((m) => (m.id === id ? { ...m, isMemoSaved: true } : m))
    );
    // textarea  
    setTimeout(() => {
      const el = textAreaRefs.current[id];
      if (el) autoResize(el);
    }, 0);
  };
  
  //  
  const handleEditMemo = (id: string) => {
    setMemos((prev) =>
      prev.map((m) => (m.id === id ? { ...m, isMemoSaved: false } : m))
    );
  };
  //  
  const handleDeleteMemo = (id: string) => {
    setMemos((prev) => prev.filter((m) => m.id !== id));
  };
  //  
  const handleMemoChange = (
    id: string,
    field: 'pageNumber' | 'memo',
    value: string
  ) => {
    setMemos((prev) =>
      prev.map((m) => (m.id === id ? { ...m, [field]: value } : m))
    );
  };
  

  // (5)  
  const handleUploadClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      const file = e.target.files[0];
      setUploadedImage(file);
      const previewUrl = URL.createObjectURL(file);
      setBackgroundImage(previewUrl);
    }
  };

  // (6) 
  const handleSave = (
    e?: React.MouseEvent<HTMLButtonElement>,
    navigateAfterSave = false,
    skipValidation = false,
    overrideEndDate?: string,
    overrideReadingStatus?: string
  ) => {
    if (e) e.preventDefault();
    if (isSaving) return;
    setIsSaving(true);
    
    // 
    if (title.trim() === '') {
      setTitleError(true);
      if (titleInputRef.current) {
        titleInputRef.current.focus();
        titleInputRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
      setIsSaving(false);
      return;
    }

    if (startDate.trim() === '') {
      setStartDateError(true);
      if (startDateRef.current) {
        startDateRef.current.focus();
        startDateRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
      setIsSaving(false);
      return;
    }
    if (inputAuthor.trim() === '') {
      setInputAuthorError(true);
      if (inputAuthorRef.current) {
        inputAuthorRef.current.focus();
        inputAuthorRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
      setIsSaving(false);
      return;
    }
    if (genre.trim() === '') {
      setGenreError(true);
      if (genreRef.current) {
        genreRef.current.focus();
        genreRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
      setIsSaving(false);
      return;
    }
  
    const nickname = localStorage.getItem('nickname');
    if (!nickname) {
      alert(' .');
      return;
    }
    const defaultAvatar = '/default_avatar.png';
    const email = localStorage.getItem('email'); // :   
    const postData = {
      title,
      startDate,
      readingStatus: overrideReadingStatus !== undefined ? overrideReadingStatus : readingStatus,
      author: { name: nickname, avatar: defaultAvatar, email: email },
      inputAuthor,
      genre,
      publisher,
      summary,
      review,
      endDate: overrideEndDate !== undefined ? overrideEndDate : endDate,
      memos,
      email: email,
    };

    if (_uploadedImage) {
      const formData = new FormData();
      formData.append('image', _uploadedImage);
      fetch(`${BASE_URL}/api/upload-post-background`, {
        method: 'POST',
        body: formData,
      })
        .then((res) => {
          if (!res.ok) throw new Error('Background image upload failed');
          return res.json();
        })
        .then((data) => {
          // data.imageUrl   URL
          (postData as any).titleBackgroundImage = data.imageUrl;
          // : effectivePostId, requestMethod, requestUrl  
          const effectivePostId = postId || savedPostId;
          const requestMethod = effectivePostId ? 'PUT' : 'POST';
          const requestUrl = effectivePostId
            ? `${BASE_URL}/posts/${effectivePostId}`
            : `${BASE_URL}/posts`;

          fetch(requestUrl, {
            method: requestMethod,
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(postData),
          })
            .then((res) => {
              if (!res.ok) throw new Error('Save failed');
              return res.json();
            })
            .then((data) => {
              if (!effectivePostId && data.id) {
                setSavedPostId(String(data.id));
              }
              setIsSaved(true);
              window.dispatchEvent(new Event('postsUpdated'));
              if (navigateAfterSave) {
                navigate('/booknote');
            }
            })
            .catch((error) => {
              console.error('Error saving post:', error);
            })
            .finally(() => {
              setIsSaving(false);
          });
        })
        .catch((error) => {
          console.error(error);
          alert('   .');
          setIsSaving(false);
        });
    } else {
      //      
      const effectivePostId = postId || savedPostId;
      const requestMethod = effectivePostId ? 'PUT' : 'POST';
      const requestUrl = effectivePostId
        ? `${BASE_URL}/posts/${effectivePostId}`
        : `${BASE_URL}/posts`;

    fetch(requestUrl, {
      method: requestMethod,
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(postData),
    })
    .then((res) => {
      if (!res.ok) throw new Error('Save failed');
      return res.json();
    })
    .then((data) => {
      //      id   id .
      if (!effectivePostId && data.id) {
        setSavedPostId(String(data.id));
      }
      setIsSaved(true);
      window.dispatchEvent(new Event('postsUpdated'));
      if (navigateAfterSave) {
        navigate('/booknote');
      }
    })
    .catch((error) => {
      console.error('Error saving post:', error);
    })
    .finally(() => {
      setIsSaving(false);
    });
  }
      
    setShowToast(true);
    setTimeout(() => setShowToast(false), 2000);
  };

  

  useEffect(() => {
    const saveDraft = () => {
      const draft = {
        title,
        startDate,
        inputAuthor,
        genre,
        publisher,
        summary,
        review,
        endDate,
        memos,
        backgroundImage,
        readingStatus,
      };
      localStorage.setItem('createPostDraft', JSON.stringify(draft));
    };
  
    window.addEventListener('beforeunload', saveDraft);
    return () => {
      //     
      saveDraft();
      window.removeEventListener('beforeunload', saveDraft);
    };
  }, [title, startDate, inputAuthor, genre, publisher, summary, review, endDate, memos, backgroundImage, readingStatus]);
  
  
  
  

  return (
    <div
      style={{
        backgroundColor: '#F7F5F5',
        minHeight: '100vh',
        paddingBottom: '120px', //    
      }}
    >
      {/*  :   +    */}
      <div
        style={{
          height: '300px',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          background: backgroundImage ? `linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),url(${backgroundImage}) no-repeat center/cover` : '#999',
          color: '#fff',
          marginTop: '60px'
        }}
      >
        {/*   */}
        <input
          ref={titleInputRef}
          type="text"
          value={title}
          onChange={(e) => {
            setTitle(e.target.value);
            setIsSaved(false); //     .
            if (e.target.value.trim() !== '') {
              setTitleError(false);
            }
          }}
          placeholder=" ."
          className="title-input"
          style={{
            fontSize: '24px',
            border: 'none',
            outline: 'none',
            backgroundColor: 'transparent',
            color: '#fff',
            textAlign: 'center',
            marginBottom: '15px',
            width: '60%',
            borderBottom: titleError ? '2px solid red' : '2px solid #fff',
          }}
        />
        {/*    */}
        <button
          onClick={handleUploadClick}
          style={{
            backgroundColor: '#fff',
            color: '#000',
            border: 'none',
            borderRadius: '4px',
            padding: '8px 16px',
            cursor: 'pointer',
            fontSize: '12px',
          }}
        >
          Upload Photo
        </button>
        <input
          type="file"
          ref={fileInputRef}
          style={{ display: 'none' }}
          accept="image/*"
          onChange={handleFileChange}
        />
      </div>

      {/*   */}
      <div style={{ maxWidth: '1200px', margin: '0 auto', padding: '40px 20px' }}>
        {/* Book Information */}
        <div style={{ marginBottom: '70px' }}>
          <h2 style={{ fontSize: '25px', fontWeight: 'bold', marginBottom: '25px' }}>
            Book Information
          </h2>
          <div
            style={{
              display: 'grid',
              gridTemplateColumns: '1fr 2fr', // /
              gap: '20px',
            }}
          >
            {/* :   , , ,  */}
            <div style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
              {/*    */}
              <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                <label style={{ width: '100px', fontWeight: 'bold' }}>  </label>
                <input
                  type="date"
                  ref={startDateRef}
                  value={startDate}
                  onChange={(e) => {
                    setStartDate(e.target.value);
                    setStartDateError(false);
                  }}
                  style={{
                    flex: 1,
                    padding: '8px',
                    border: startDateError ? '2px solid red' : '1px solid #ccc',
                  }}
                />
              </div>
              {/*  */}
              <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                <input
                  type="text"
                  ref={inputAuthorRef}
                  value={inputAuthor}
                  onChange={(e) => {
                    setInputAuthor(e.target.value);
                    setInputAuthorError(false);
                  }}
                  placeholder=" ."
                  style={{
                    flex: 1,
                    padding: '8px',
                    border: inputAuthorError ? '2px solid red' : '1px solid #ccc',
                  }}
                />
              </div>
              {/*  */}
              <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                {isDirectGenre ? (
                  <input
                    type="text"
                    value={customGenre}
                    onChange={(e) => {
                      setCustomGenre(e.target.value);
                      setGenre(e.target.value);
                      setGenreError(false);
                    }}
                    placeholder=" "
                    style={{
                      flex: 1,
                      padding: '8px',
                      border: genreError ? '2px solid red' : '1px solid #ccc',
                    }}
                  />
                ) : (
                  <select
                    value={genre}
                    ref={genreRef}
                    onChange={(e) => {
                      if (e.target.value === ' ') {
                        setIsDirectGenre(true);
                        setCustomGenre("");
                        setGenre("");
                        setGenreError(false);
                      } else {
                        setGenre(e.target.value);
                        setGenreError(false);
                      }
                    }}
                    style={{
                      flex: 1,
                      padding: '8px',
                      border: genreError ? '2px solid red' : '1px solid #ccc',
                    }}
                  >
                    <option value=""> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="/">/</option>
                    <option value=" "> </option>
                  </select>
                )}
              </div>

              {/*  */}
              <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                <input
                  type="text"
                  value={publisher}
                  onChange={(e) => setPublisher(e.target.value)}
                  placeholder=" ."
                  style={{
                    flex: 1,
                    padding: '8px',
                    border: '1px solid #ccc',
                  }}
                />
              </div>
            </div>
            {/* : Summary */}
            <div style={{ display: 'flex', flexDirection: 'column' }}>
              <label style={{ fontWeight: 'bold', marginBottom: '8px' }}>Summary</label>
              <textarea
                value={summary || ''}
                onChange={(e) => setSummary(e.target.value)}
                placeholder="  ."
                style={{
                  flex: 1,
                  padding: '10px',
                  border: '1px solid #ccc',
                  resize: 'none',
                }}
              />
            </div>
          </div>
        </div>

        {/* Memo */}
        <div style={{ marginBottom: '70px' }}>
          <div
            style={{
              display: 'flex',
              gap: '15px', 
              alignItems: 'center',
            }}
          >
            <h2 style={{ fontSize: '22px', fontWeight: 'bold' }}>Memo</h2>
            <button
              onClick={handleAddMemo}
              style={{
                fontSize: '14px',
                backgroundColor: '#979797',
                color: '#fff',
                border: 'none',
                borderRadius: '10px',
                padding: '6px 14px',
                cursor: 'pointer',
              }}
            >
               
            </button>
          </div>

          {/*    */}
          {memos.map((m) => (
            <div key={m.id}>
              {/* (A)  :   ,   */}
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'flex-end', //  
                  gap: '15px',
                  marginBottom: '8px',       //   
                  
                }}
              >
                {/*     :  +   */}
                {!m.isMemoSaved ? (
                  <>
                    <button onClick={() => handleDeleteMemo(m.id)}
                      style={{
                        backgroundColor: '#fff',
                        color: '#333',
                        border: '1px solid #999',
                        borderRadius: '10px',
                        padding: '5px 20px',
                        cursor: 'pointer',
                      }}
                      ></button>
                    <button onClick={() => handleRegisterMemo(m.id)}
                      style={{
                        backgroundColor: '#000',
                        color: '#fff',
                        border: 'none',
                        borderRadius: '10px',
                        padding: '5px 20px',
                        cursor: 'pointer',
                      }}
                      ></button>
                  </>
                ) : (
                  /*   :  +   */
                  <>
                    <button onClick={() => handleDeleteMemo(m.id)}
                      style={{
                        backgroundColor: '#fff',
                        color: '#333',
                        border: '1px solid #999',
                        borderRadius: '10px',
                        padding: '5px 20px',
                        cursor: 'pointer',
                      }}></button>
                    <button onClick={() => handleEditMemo(m.id)}
                      style={{
                        backgroundColor: '#999',
                        color: '#fff',
                        border: 'none',
                        borderRadius: '10px',
                        padding: '5px 20px',
                        cursor: 'pointer',
                      }}></button>
                  </>
                )}
              </div>

              {/* (B)    */}
              <div
                style={{
                  position: 'relative',
                  backgroundColor: '#fff',
                  boxShadow: '4px 4px 4px rgba(0,0,0,0.25)',
                  marginBottom: '20px',
                  padding: '20px',
                }}
              >
                {/*      */}
                <div
                  style={{
                    position: 'absolute',
                    top: -12,
                    left: 10,
                    width: '30px',
                    height: '30px',
                    backgroundColor: 'red',
                    backgroundImage: 'url("/bookmark_icon.png")',
                    backgroundSize: 'cover',
                  }}
                />

                {/*   textarea */}
                <textarea
                  ref={(el) => (textAreaRefs.current[m.id] = el)}
                  value={m.memo || ''}
                  onChange={(e) => handleMemoChange(m.id, 'memo', e.target.value)}
                  disabled={m.isMemoSaved}
                  placeholder="  ."
                  style={{
                    width: '100%',
                    minHeight: '100px',
                    border: 'none',
                    outline: 'none',
                    resize: 'none',
                    marginTop: '20px',
                    backgroundColor: '#fff',
                  }}
                />

                {/*   ( ) */}
                <div
                  style={{
                    position: 'absolute',
                    bottom: '10px',
                    right: '10px',
                    fontWeight: 'bold',
                    color: '#666',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '6px',
                  }}
                >
                  <span>Page</span>
                  <input
                    type="text"
                    value={m.pageNumber || ''}
                    onChange={(e) => handleMemoChange(m.id, 'pageNumber', e.target.value)}
                    disabled={m.isMemoSaved}
                    style={{
                      textAlign: 'center',
                      width: '80px',
                      border: 'none',
                      borderBottom: '1px solid #ccc',
                      backgroundColor: m.isMemoSaved ? '#fff' : '#fff',
                      outline: 'none',
                    }}
                  />
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Review */}
        <div style={{ marginBottom: '50px' }}>
          <h2 style={{ fontSize: '22px', fontWeight: 'bold', marginBottom: '20px' }}>Review</h2>
          <div
            style={{
              backgroundColor: '#fff',
              boxShadow: '4px 4px 4px rgba(0,0,0,0.25)',
              padding: '20px',
            }}
          >
            <textarea
              value={review}
              onChange={(e) => setReview(e.target.value)}
              placeholder=" ."
              style={{
                width: '100%',
                minHeight: '100px',
                border: 'none',
                outline: 'none',
                resize: 'none',
              }}
            />
          </div>
        </div>

        {/*    */}
        <div style={{ textAlign: 'right', marginTop: '50px' }}>
          <label style={{ fontWeight: 'bold', marginRight: '10px' }}>  </label>
          <div
            style={{
              display: 'inline-block',
              position: 'relative',
            }}
          >
            <input
              type="date"
              value={endDate}
              onChange={(e) => {
                const newEndDate = e.target.value;
                setEndDate(newEndDate);
                handleCloseBookDate(e);
                //  endDate  '',  '' override 
                handleSave(undefined, false, true, newEndDate, newEndDate ? '' : '');
              }}
              style={{
                padding: '8px',
                border: '1px solid #ccc',
                backgroundColor: '#fff',
                cursor: 'pointer',
              }}
            />
            {/*      background-image pseudo-element  */}
          </div>
        </div>
      </div>

      {/* /  (  ) */}
      {showToast && (
        <div
          style={{
            fontWeight: 'bold',
            position: 'fixed',
            top: '100px',
            left: '50%',
            transform: 'translateX(-50%)',
            backgroundColor: '#fff',
            border: '1px solid #979797',
            color: '#000',
            padding: '12px 24px',
            borderRadius: '4px',
            zIndex: 1000,
          }}
        >
             .
        </div>
      )}
      {showConfirmBackModal && (
        <div
          style={{
            position: 'fixed',
            top: 0, left: 0, right: 0, bottom: 0,
            backgroundColor: 'rgba(0,0,0,0.4)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <div
            style={{
              width: '320px',
              padding: '20px',
              backgroundColor: '#fff',
              borderRadius: '8px',
              textAlign: 'center',
            }}
          >
            <p style={{ marginBottom: '20px' }}>
                .<br />?
            </p>
            <button
              onClick={confirmGoBack}
              style={{
                marginRight: '10px',
                padding: '8px 16px',
                backgroundColor: '#333',
                color: '#fff',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer',
              }}
            >
              
            </button>
            <button
              onClick={cancelGoBack}
              style={{
                padding: '8px 16px',
                backgroundColor: '#fff',
                color: '#333',
                border: '1px solid #ccc',
                borderRadius: '4px',
                cursor: 'pointer',
              }}
            >
              
            </button>
          </div>
        </div>
      )}
      {showCongratsModal && (
        
        <div
          style={{
            position: 'fixed',
            top: 0, left: 0, right: 0, bottom: 0,
            backgroundColor: 'rgba(0,0,0,0.4)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <div
            style={{
              width: '300px',
              padding: '40px',
              backgroundColor: '#fff',
              borderRadius: '8px',
              textAlign: 'center',
            }}
          >
            <img
                src="/success_icon.png"
                alt="success"
                style={{
                  width: '100px',
                  marginBottom: '20px',
                  display: 'block',
                  marginLeft: 'auto',
                  marginRight: 'auto',
                }}
              />
            <p
              style={{
                marginTop: '30px',
                marginBottom: '30px',
                fontSize: '20px',
                fontWeight: 'bold',
              }}
            >
               !
            </p>
            <button
              onClick={handleCloseCongratsModal}
              style={{
                padding: '8px 60px',
                backgroundColor: '#333',
                color: '#fff',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer',
              }}
            >
              
            </button>
          </div>
        </div>
      )}

      {/*     ( / ) */}
      <div
        style={{
          position: 'fixed',
          bottom: 0,
          left: 0,
          width: '100%',
          backgroundColor: '#C4C4C4',
          display: 'flex',
          justifyContent: 'flex-end',
          alignItems: 'center',
          padding: '10px 20px',
          zIndex: 999,
        }}
      >
        <button
          onClick={handleGoBack}
          style={{
            backgroundColor: '#fff',
            color: '#4C4C4C',
            border: '1px solid #D6D6D6',
            borderRadius: '20px',
            padding: '5px 16px',
            marginRight: '15px',
            cursor: 'pointer',
          }}
        >
          
        </button>
        <button
          onClick={handleSave}
          disabled={isSaving} //    
          style={{
            backgroundColor: '#000',
            color: '#fff',
            borderRadius: '20px',
            padding: '5px 16px',
            cursor: 'pointer',
          }}
        >
          
        </button>
      </div>

      {showDraftModal && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0, 0, 0, 0.4)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            zIndex: 1000,
          }}
        >
          <div
            style={{
              width: '300px',
              backgroundColor: '#fff',
              borderRadius: '8px',
              padding: '20px',
              textAlign: 'center',
            }}
          >
            <p style={{ marginBottom: '20px', fontSize: '16px' }}>
                  . <br/>
              ?
            </p>
            <button
              onClick={() => {
                //     .
                setTitle(draftData.title || '');
                setStartDate(draftData.startDate || '');
                setInputAuthor(draftData.inputAuthor || '');
                setGenre(draftData.genre || '');
                setPublisher(draftData.publisher || '');
                setSummary(draftData.summary || '');
                setReview(draftData.review || '');
                setEndDate(draftData.endDate || '');
                setMemos(
                  draftData.memos ||
                    [{ id: String(Date.now()), pageNumber: '', memo: '', isMemoSaved: false }]
                );
                setBackgroundImage(draftData.backgroundImage || '');
                setReadingStatus(draftData.readingStatus || '');
                setShowDraftModal(false);
                localStorage.removeItem('createPostDraft');
              }}
              style={{
                marginRight: '10px',
                padding: '8px 16px',
                backgroundColor: '#000',
                color: '#fff',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer',
              }}
            >
              
            </button>
            <button
              onClick={() => {
                setShowDraftModal(false);
                localStorage.removeItem('createPostDraft');
              }}
              style={{
                padding: '8px 16px',
                backgroundColor: '#ccc',
                color: '#000',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer',
              }}
            >
              
            </button>
          </div>
        </div>
      )}

    </div>
  );
};

export default CreatePostPage;
