import React, { useState, useEffect } from 'react';
const BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8083';

const SettingsPage: React.FC = () => {
  // :   state
  const [nickname, setNickname] = useState('');
  const [displayNickname, setDisplayNickname] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [aboutMe, setAboutMe] = useState('');
  const [profileImage, setProfileImage] = useState('/default_avatar.png');
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [postCount, setPostCount] = useState(0);
  const [completedCount, setCompletedCount] = useState(0);
  const [displayAboutMe, setDisplayAboutMe] = useState('');
  const [showDeleteAccountModal, setShowDeleteAccountModal] = useState(false);
  const [deletionPassword, setDeletionPassword] = useState('');
  const [deletionError, setDeletionError] = useState('');
  const [deletionComplete, setDeletionComplete] = useState(false);
  const [isPasswordVisible, setIsPasswordVisible] = useState(false);

  const [originalData, setOriginalData] = useState({
    nickname: '',
    aboutMe: '',
    profileImage: '/default_avatar.png'
  });


  //     
const confirmDeleteAccount = async () => {
  if (deletionPassword.trim() === '') {
    setDeletionError(" .");
    return;
  }
  const email = localStorage.getItem('email');
  if (!email) {
    setDeletionError(" .");
    return;
  }
  try {
    const res = await fetch(`${BASE_URL}/api/auth/delete-account?email=${email}`, {
      method: 'DELETE',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ password: deletionPassword })
    });
    if (!res.ok) {
      setDeletionError("  .   .");
      return;
    }
    //  
    setDeletionComplete(true);
    localStorage.removeItem('email');
    localStorage.removeItem('nickname');
    localStorage.removeItem('profileImage');
    localStorage.removeItem('goalEmail');            // :     
    localStorage.removeItem('yearlyGoal');             // :    
    localStorage.removeItem('yearlyAchieved');         // :   
    localStorage.removeItem('monthlyGoal');            // :    
    const currentYear = new Date().getFullYear();
    localStorage.removeItem(`yearlyGoal_${currentYear}`); // :    
    for (let m = 1; m <= 12; m++) {                       // :   
      localStorage.removeItem(`monthlyGoal_${currentYear}_${m}`);
    }
  } catch (error) {
    console.error(error);
    setDeletionError("  .");
  }
};

  //     (   )
  const handleUploadNewProfile = () => {
    document.getElementById('profile-file-input')?.click();
  };

  const [showDeleteButton, setShowDeleteButton] = useState(false);
  
  const handleDeleteProfileImage = async () => {
    const email = localStorage.getItem('email');
    if (!email) {
      alert(' .');
      return;
    }
    try {
      const res = await fetch(`${BASE_URL}/api/settings/${email}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        //  avatarUrl   (passwordConfirmation )
        body: JSON.stringify({ avatarUrl: '/default_avatar.png' }),
      });
      if (!res.ok) throw new Error('   ');
  
      setProfileImage('/default_avatar.png');
      localStorage.setItem('profileImage', '/default_avatar.png');
      window.dispatchEvent(new Event('profileImageUpdated'));
      alert('  .');
    } catch (error) {
      console.error(error);
      alert('   .');
    }
  };
  

  //     
  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      const file = e.target.files[0];
      const formData = new FormData();
      formData.append('email', email);
      formData.append('profileImage', file);

      try {
        const res = await fetch(`${BASE_URL}/api/upload-profile`, {
          method: 'POST',
          body: formData,
        });
        if (!res.ok) throw new Error('   ');

        const data = await res.json();
        const uploadedUrl = data.imageUrl;

        // 1) SettingsPage   
        setProfileImage(uploadedUrl);
        // 2)     Header  
        localStorage.setItem('profileImage', uploadedUrl);
        window.dispatchEvent(new Event('profileImageUpdated'));

        alert('   .');
      } catch (error) {
        console.error(error);
        alert('   .');
      }
    }
  };


  // / 
  const handleSave = () => {
    //   
    if (password.trim() === '') {
      alert('     .');
      return;
    }

    //   payload  (  )
    const payload = {
      nickname,
      aboutMe,
      //     
      passwordConfirmation: password, //   
    };

    // API  (PUT  )
    fetch(`${BASE_URL}/api/settings/${email}`, {
      method: 'PUT', //      (PUT  POST)
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payload),
    })
      .then((res) => {
        //        
        if (!res.ok) {
          return res.text().then((errorMessage) => {
            throw new Error(errorMessage);
          });
        }
        return res.json();
      })
      .then((updatedUser) => {
        // 3) localStorage   Header nickname   
        localStorage.setItem('nickname', updatedUser.nickname);

        // display  (     )
        setDisplayNickname(updatedUser.nickname);
        setDisplayAboutMe(updatedUser.aboutMe);

        // 4)  
        window.location.reload();
      })
      .catch((err) => {
        console.error('Error saving settings:', err);
        alert(err.message);
      });
  };

  useEffect(() => {
    const storedEmail = localStorage.getItem('email');
    if (!storedEmail) {
      console.error('No email found in localStorage');
      return;
    }
    fetch(`${BASE_URL}/api/settings/${storedEmail}`)
      .then((res) => {
        if (!res.ok) throw new Error('Failed to fetch user settings');
        return res.json();
      })
      .then((data) => {
        console.log('Fetched user settings:', data);
        setNickname(data.nickname || '');
        setDisplayNickname(data.nickname || '');
        setEmail(data.email || '');
        setPassword('');
        setAboutMe(data.aboutMe || '');
        setDisplayAboutMe(data.aboutMe || '');
        setProfileImage(data.avatarUrl || '/default_avatar.png');
        setPostCount(data.postCount || 0);
        setCompletedCount(data.completedCount || 0);
        
        //   
        setOriginalData({
          nickname: data.nickname || '',
          aboutMe: data.aboutMe || '',
          profileImage: data.avatarUrl || '/default_avatar.png'
        });
      })
      .catch((err) => console.error('Error fetching user settings:', err));
  }, []);

  const handleCancel = () => {
     //     
    setNickname(originalData.nickname);
    setDisplayNickname(originalData.nickname);
    setAboutMe(originalData.aboutMe);
    setDisplayAboutMe(originalData.aboutMe);
    setProfileImage(originalData.profileImage);
    setPassword('');
  };


  useEffect(() => {
    const storedEmail = localStorage.getItem('email');
    if (!storedEmail) {
      console.error('No email found in localStorage');
      return;
    }
    fetch(`${BASE_URL}/api/settings/${storedEmail}`)
      .then((res) => {
        if (!res.ok) throw new Error('Failed to fetch user settings');
        return res.json();
      })
      .then((data) => {
        console.log('Fetched user settings:', data);
        setNickname(data.nickname || '');
        setDisplayNickname(data.nickname || '');
        setEmail(data.email || '');
        setPassword('');
        setAboutMe(data.aboutMe || '');
        setDisplayAboutMe(data.aboutMe || '');
        setProfileImage(data.avatarUrl || '/default_avatar.png');
        setPostCount(data.postCount || 0);
        setCompletedCount(data.completedCount || 0);

      })
      .catch((err) => console.error('Error fetching user settings:', err));
  }, []);


  return (
    <div style={{ backgroundColor: '#f5f5f5', }}>
      <div
        style={{
          maxWidth: '1200px',
          padding: '100px 60px',
          margin: '0 auto',
          minHeight: '100vh',
        }}
      >
        {/*  : Settings  */}
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: '20px',
          }}
        >
          <h2 style={{ fontWeight: 'bold', fontSize: '40px', margin: 0, color: '#434343' }}>Settings</h2>
        </div>
        <hr style={{ border: '1px solid #434343', marginBottom: '40px' }} />

        <div
          style={{
            display: 'flex',
            gap: '60px',
          }}
        >
          {/* :   */}
          <div style={{ width: '300px' }}>
            {/*   */}
            <div
              style={{
                position: 'relative',
                width: '180px',
                height: '180px',
                borderRadius: '50%',
                overflow: 'hidden',
                margin: '30px auto 20px auto',
                border: '1px solid #ccc',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
              onMouseEnter={() => setShowDeleteButton(true)}
              onMouseLeave={() => setShowDeleteButton(false)}
            >
              {profileImage ? (
                <img
                  src={profileImage}
                  alt="profile"
                  style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                />
              ) : (
                <div style={{ color: '#999' }}>No Image</div>
              )}
              {showDeleteButton && (
                <button
                  onClick={handleDeleteProfileImage}
                  style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    backgroundColor: 'rgba(0,0,0,0.6)',
                    color: '#fff',
                    border: 'none',
                    borderRadius: '24px',
                    padding: '5px 20px',
                    cursor: 'pointer',
                  }}
                >
                  
                </button>
                )}
            </div>

            {/*  */}
            <div style={{ textAlign: 'center', fontSize: '20px', fontWeight: 'bold', marginBottom: '10px' }}>
              {displayNickname}
            </div>

            {/*   */}
            <div style={{ textAlign: 'center', fontSize: '14px', color: '#777', marginBottom: '40px' }}>
              {displayAboutMe || ' .'}
            </div>

            {/*   /    */}
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                marginBottom: '20px',
                fontSize: '14px',
                color: '#333',
              }}
            >
              <div style={{ textAlign: 'center' }}>
                <div style={{ fontWeight: 'bold', fontSize: '18px' }}>{postCount}</div>
                <div> </div>
              </div>
              <div style={{ borderLeft: '2px solid #ccc', height: '40px', margin: '0 20px' }}></div>
              <div style={{ textAlign: 'center' }}>
                <div style={{ fontWeight: 'bold', fontSize: '18px' }}>{completedCount}</div>
                <div> </div>
              </div>
            </div>

            {/*   */}
            <button
              onClick={handleUploadNewProfile}
              style={{
                display: 'block',
                margin: '0 auto',
                backgroundColor: '#6B705C',
                border: '1px solid #ccc',
                borderRadius: '10px',
                padding: '8px 20px',
                cursor: 'pointer',
                fontSize: '16px',
                color: '#fff',

              }}
            >
              Upload new Profile
            </button>
            <input
              id="profile-file-input"
              type="file"
              accept="image/*"
              style={{ display: 'none' }}
              onChange={handleFileChange}
            />
          </div>

          {/* :   ()  */}
          <div style={{ flex: 1 }}>
            {/* : BASIC INFO + Cancel/Save  */}
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              marginBottom: '20px',
            }}>
              <h3 style={{ margin: 0, fontWeight: 'bold', fontSize: '20px' }}>BASIC INFO</h3>
              <div style={{ display: 'flex', gap: '10px' }}>
                <button
                  onClick={handleCancel}
                  style={{
                    backgroundColor: '#fff',
                    border: '1px solid #ccc',
                    borderRadius: '4px',
                    padding: '8px 16px',
                    cursor: 'pointer',
                    fontSize: '14px',
                  }}
                >
                  CANCEL
                </button>
                <button
                  onClick={handleSave}
                  style={{
                    backgroundColor: '#A5A58D',
                    color: '#fff',
                    border: 'none',
                    borderRadius: '4px',
                    padding: '8px 16px',
                    cursor: 'pointer',
                    fontSize: '14px',
                  }}
                >
                  SAVE
                </button>
              </div>
            </div>

            <hr style={{ border: '0.5px solid #ccc', marginBottom: '20px', width: '100%' }} />

            {/*   */}
            <div style={{ display: 'flex', flexDirection: 'column', gap: '16px', width: '100%' }}>
              {/* Nickname*/}
              <div style={{ width: '100%', marginBottom: '20px' }}>
                <label style={{ display: 'block', fontSize: '15px', fontWeight: 'bold', marginBottom: '4px' }}>Nickname</label>
                <input
                  type="text"
                  value={nickname}
                  onChange={(e) => setNickname(e.target.value)}
                  style={{ width: '100%', padding: '10px', border: '1px solid #ccc', borderRadius: '4px' }}
                />
              </div>

              {/* Email */}
              <div style={{ width: '100%' }}>
                <label style={{ display: 'block', fontSize: '15px', fontWeight: 'bold', marginBottom: '4px' }}>
                  E-mail
                </label>
                <input
                  type="email"
                  value={email}
                  disabled // 
                  onChange={(e) => setEmail(e.target.value)}
                  style={{
                    width: '100%',
                    padding: '10px',
                    border: '1px solid #ccc',
                    borderRadius: '4px',
                    backgroundColor: '#f9f9f9',
                    marginBottom: '20px'
                  }}
                />
              </div>

              {/* Password */}
              <div style={{ width: '100%', position: 'relative' }}>
                <label style={{ display: 'block', fontSize: '15px', fontWeight: 'bold', marginBottom: '4px' }}>
                  Password
                </label>
                <input
                  type={isPasswordVisible ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  style={{
                    width: '100%',
                    padding: '10px',
                    paddingRight: '40px', //   
                    border: '1px solid #ccc',
                    borderRadius: '4px',
                    marginBottom: '20px'
                  }}
                />
                <img
                  src={isPasswordVisible ? '/show_icon.png' : '/hide_icon.png'}
                  alt={isPasswordVisible ? ' ' : ' '}
                  onClick={() => setIsPasswordVisible(!isPasswordVisible)}
                  style={{
                    position: 'absolute',
                    right: '10px',
                    top: '45%',
                    transform: 'translateY(-50%)',
                    width: '20px',
                    height: '20px',
                    cursor: 'pointer',
                  }}
                />
                <div style={{ color: 'red', fontSize: '12px', marginTop: '-15px', marginBottom: '20px' }}>
                     SAVE  .
                </div>
              </div>


              {/* ABOUT ME */}
              <div style={{ width: '100%' }}>
                <label style={{ fontWeight: 'bold', marginBottom: '4px', display: 'block' }}>
                  ABOUT ME
                </label>
                <hr style={{ border: '0.5px solid #ccc', margin: '20px 0' }} />
                <textarea
                  value={aboutMe}
                  placeholder=' .'
                  onChange={(e) => setAboutMe(e.target.value)}
                  style={{
                    width: '100%',
                    height: '80px',
                    padding: '10px',
                    border: '1px solid #ccc',
                    borderRadius: '4px',
                    resize: 'none',
                  }}
                />
                <div style={{ textAlign: 'right', marginTop: '8px' }}>
                  <span
                    style={{ cursor: 'pointer', color: '#0000EE', fontSize: '14px', textDecoration: 'underline', }}
                    onClick={() => {
                      setShowDeleteAccountModal(true);
                      setDeletionError('');
                      setDeletionPassword('');
                      setDeletionComplete(false);
                    }}
                  >
                     
                  </span>
                </div>
              </div>

              
            </div>

          </div>
          
          {showDeleteAccountModal && (
            <div style={{
              position: 'fixed',
              inset: 0,
              backgroundColor: 'rgba(0,0,0,0.5)',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              zIndex: 9999,
            }}>
            <div style={{
                position: 'relative', // X     
                backgroundColor: deletionComplete ? '#D8E8E2' : '#F4DADA',
                border: `2px solid ${deletionComplete ? '#BFD8CF' : '#E2BFBF'}`,
                borderRadius: '8px',
                padding: '30px',
                width: '380px',
                textAlign: 'center',
              }}>
              {/* X  ( ) */}
              <button
                onClick={() => {
                  setShowDeleteAccountModal(false);
                  setDeletionPassword('');
                  setDeletionError('');
                }}
                style={{
                  position: 'absolute',
                  top: '10px',
                  right: '10px',
                  background: 'transparent',
                  border: 'none',
                  fontSize: '20px',
                  cursor: 'pointer',
                }}
              >
                X
              </button>
                {!deletionComplete ? (
                  <>
                    {/*   + caution   */}
                    <div style={{
                      display: 'flex',
                      alignItems: 'center',
                      gap: '8px',
                      fontSize: '20px',
                      fontWeight: 'bold',
                      marginBottom: '14px',
                    }}>
                      <img src="/caution_icon.png" alt="caution" style={{ width: 24, height: 24 }} />
                       ?
                    </div>

                    {/*    */}
                    <div style={{
                      fontSize: '13px',
                      color: '#555',
                      marginBottom: '20px',
                      lineHeight: '1.5',
                      textAlign: 'left',
                      marginLeft: '35px'
                    }}>
                           .<br />
                           .<br />
                          .  ?
                    </div>

                    <div style={{ position: 'relative', marginBottom: '10px' }}>
                      <input
                        type={isPasswordVisible ? 'text' : 'password'}
                        placeholder=" "
                        value={deletionPassword}
                        onChange={(e) => setDeletionPassword(e.target.value)}
                        style={{
                          width: '100%',
                          padding: '10px 40px 10px 10px',  //    
                          border: '1px solid #ccc',
                          borderRadius: '4px',
                        }}
                      />
                      <img
                        src={isPasswordVisible ? '/show_icon.png' : '/hide_icon.png'}
                        alt={isPasswordVisible ? ' ' : ' '}
                        style={{
                          position: 'absolute',
                          right: '10px',
                          top: '50%',
                          transform: 'translateY(-50%)',
                          width: '20px',
                          height: '20px',
                          cursor: 'pointer',
                        }}
                        onClick={() => setIsPasswordVisible(!isPasswordVisible)}
                      />
                    </div>
                    {deletionError && (
                      <div style={{ color: 'red', fontSize: '13px', marginBottom: '20px' }}>
                        {deletionError}
                      </div>
                    )}

                    <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                      <button
                        style={{
                          width: '70px',
                          backgroundColor: '#5B5F4A',
                          color: '#fff',
                          border: 'none',
                          borderRadius: '8px',
                          padding: '5px',
                          cursor: 'pointer',
                          fontSize: '15px'
                        }}
                        onClick={confirmDeleteAccount}
                      >
                        
                      </button>
                    </div>
                  </>
                ) : (
                  <>
                    {/*    + check   */}
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: '8px',
                        fontSize: '20px',
                        fontWeight: 'bold',
                        marginBottom: '20px',
                      }}>
                      <img src="/check_icon.png" alt="success" style={{ width: 24, height: 24 }} />
                        .
                    </div>

                    <p style={{ marginBottom: '30px', fontSize: '15px', lineHeight: 1.5}}>
                        .<br />
                        , <br />
                         .
                    </p>

                    <button
                      style={{
                        width: '140px',
                        backgroundColor: '#5B5F4A',
                        color: '#fff',
                        border: 'none',
                        borderRadius: '4px',
                        padding: '8px',
                        cursor: 'pointer',
                      }}
                      onClick={() => {
                        setShowDeleteAccountModal(false);
                        window.location.href = '/';
                      }}
                    >
                      
                    </button>
                  </>
                )}
              </div>
            </div>
          )}


        </div>
      </div>
    </div>
    
  );
};

export default SettingsPage;
