import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
const BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8083';


interface AuthModalProps {
  isSignUp: boolean; //    Sign Up , Log In 
  onClose: () => void;
  onLoginSuccess?: (nickname: string) => void; //      (nickname )
}


const AuthModal: React.FC<AuthModalProps> = ({ isSignUp, onClose, onLoginSuccess }) => {
  const navigate = useNavigate();

  //   
  const [activeTab, setActiveTab] = useState(isSignUp ? 'signup' : 'login');
  
  const [emailValidationMessage, setEmailValidationMessage] = useState('');
  const [emailValidationColor, setEmailValidationColor] = useState('');
  const [passwordValidationMessage, setPasswordValidationMessage] = useState(' //  8~16 .');
  const [passwordValidationColor, setPasswordValidationColor] = useState('red');
  const [nickname, setNickname] = useState('');
  const [nicknameCheckedStatus, setNicknameCheckedStatus] = useState(''); 
  const [nicknameCheckMessage, setNicknameCheckMessage] = useState('');
  const [isNicknameChecked, setIsNicknameChecked] = useState(false);
  //        
  const [loginErrorMessage, setLoginErrorMessage] = useState('');
  const [rememberMe, setRememberMe] = useState(false);

  // :        
  const [isEmailChecked, setIsEmailChecked] = useState(false);
  const [isPasswordValid, setIsPasswordValid] = useState(false);
  
  // Forgot Password / Reset Password    
  const [showForgotPasswordModal, setShowForgotPasswordModal] = useState(false);
  const [forgotEmail, setForgotEmail] = useState('');
  const [forgotEmailError, setForgotEmailError] = useState('');
  const [showResetPasswordModal, setShowResetPasswordModal] = useState(false);
  const [newPassword, setNewPassword] = useState('');
  const [newPasswordValidationMessage, setNewPasswordValidationMessage] = useState(' //  8~16 .');
  const [newPasswordValidationColor, setNewPasswordValidationColor] = useState('red');
  const [isResetPasswordVisible, setIsResetPasswordVisible] = useState(false);
  const [isLoginPasswordVisible, setIsLoginPasswordVisible] = useState(false);


  //  
  const [signUpEmail, setSignUpEmail] = useState('');
  const [signUpPassword, setSignUpPassword] = useState('');
  const [isSignUpPasswordVisible, setIsSignUpPasswordVisible] = useState(false);
  const [loginEmail, setLoginEmail] = useState('');
  const [loginPassword, setLoginPassword] = useState('');

  //    
  const [signUpSuccess, setSignUpSuccess] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');

  //   
  const isSignUpDisabled =
    !nickname.trim() ||
    !signUpEmail.trim() ||
    !signUpPassword.trim() ||
    !isEmailChecked ||
    !isPasswordValid ||
    !isNicknameChecked;


  const isLoginDisabled =
    !loginEmail.trim() ||
    !loginPassword.trim();

  //   API   
  const handleLogin = async () => {
    try {
      const response = await fetch(`${BASE_URL}/api/auth/login`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          email: loginEmail,
          password: loginPassword,
        }),
      });
      if (!response.ok) {
        setLoginErrorMessage("     .");
        return;
      }
      const result = await response.json();
  
      localStorage.setItem('email', result.email);
      localStorage.setItem('nickname', result.nickname);
  
      if (onLoginSuccess) {
        onLoginSuccess(result.nickname || '');
      }
      onClose();
      navigate('/home');
    } catch (error) {
      console.error('Login Error:', error);
      setLoginErrorMessage("    .");
    }
  };
  
  const handleNicknameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    //      
    setNickname(e.target.value);
    //      
    setNicknameCheckedStatus('');
    setNicknameCheckMessage('');
  };

  const validateNickname = (value: string) => {
    const allowedRegex = /^[a-zA-Z-0-9]+$/;
    if (!allowedRegex.test(value)) {
      return false;
    }
    if (/^[-]+$/.test(value)) {
      return value.length <= 8;
    } else {
      return value.length <= 14;
    }
  };
  

  const handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSignUpEmail(value);
    //    (  )
    const emailRegex = /^[\w-.]+@([\w-]+\.)+[\w-]{2,}$/;
    if (!emailRegex.test(value)) {
      setEmailValidationMessage("   .");
      setEmailValidationColor("red");
    } else {
      setEmailValidationMessage("");
    }
    //    
    // setIsEmailChecked(false);  //      
  };

  //  API  
  const handleSignUp = async () => {
    //          
    if (!isEmailChecked) {
      alert("   .");
      return;
    }
    try {
      const response = await fetch(`${BASE_URL}/api/auth/signup`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          nickname: nickname,
          email: signUpEmail,
          password: signUpPassword,
        }),
      });

      if (!response.ok) {
        //    (    )
        return;
      }

      //  "  !"  
      const result = await response.text();
      setSuccessMessage(result);
      setSignUpSuccess(true);
    } catch (error) {
      console.error('SignUp Error:', error);
    }
  };

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        backgroundColor: 'rgba(0,0,0,0.5)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 9999,
      }}
    >
      {/*  */}
      <div style={{ position: 'relative' }}>
        {/*   */}
        <div
          style={{
            backgroundColor: '#fff',
            borderRadius: '10px',
            width: '500px',
            padding: '50px',
            position: 'relative',
          }}
        >
          {/*  (Sign up / Log in) */}
          <div style={{ display: 'flex', marginBottom: '20px' }}>
            <div
              style={{
                flex: 1,
                textAlign: 'center',
                padding: '10px',
                cursor: 'pointer',
                borderRadius: '4px',
                backgroundColor: activeTab === 'signup' ? '#000' : '#ddd',
                color: activeTab === 'signup' ? '#fff' : '#000',
              }}
              onClick={() => setActiveTab('signup')}
            >
              Sign up
            </div>
            <div
              style={{
                flex: 1,
                textAlign: 'center',
                padding: '10px',
                cursor: 'pointer',
                borderRadius: '4px',
                backgroundColor: activeTab === 'login' ? '#000' : '#ddd',
                color: activeTab === 'login' ? '#fff' : '#000',
              }}
              onClick={() => setActiveTab('login')}
            >
              Log in
            </div>
          </div>

          {/* X  (  ) */}
          <div
            style={{
              position: 'absolute',
              top: '-60px',
              right: '-35px',
              fontSize: '40px',
              cursor: 'pointer',
              color: 'white',
            }}
            onClick={onClose}
          >
            &times;
          </div>

          {/*   */}
          {activeTab === 'signup' && (
            <>
              <h2
                style={{
                  textAlign: 'center',
                  marginBottom: '40px',
                  fontSize: '28px',
                  fontWeight: 'bold',
                }}
              >
                Sign up
              </h2>

              {/* Nickname   (,    / ) */}
              {/*    8,     14  */}
              <div style={{ marginBottom: '20px' }}>
                <label style={{ display: 'block', marginBottom: '4px' }}>
                  Nickname
                </label>
                <div style={{ display: 'flex' }}>
                  <div style={{ position: 'relative', flex: 1 }}>
                    <input
                      type="text"
                      placeholder=" "
                      style={{
                        width: '100%',
                        padding: '12px 40px 12px 12px',  //   40px    
                        border: `1px solid ${
                          nicknameCheckedStatus === "success"
                            ? "green"
                            : (nicknameCheckedStatus === "duplicate" || nicknameCheckedStatus === "invalid")
                            ? "red"
                            : "#ccc"
                        }`,
                        borderRadius: '4px'
                      }}
                      value={nickname}
                      onChange={handleNicknameChange}
                    />
                    {nicknameCheckedStatus && (
                      <img
                        src={nicknameCheckedStatus === "success" ? "check_icon.png" : "caution_icon.png"}
                        alt="validation icon"
                        style={{
                          position: 'absolute',
                          right: '10px',
                          top: '50%',
                          transform: 'translateY(-50%)',
                          width: '20px',
                          height: '20px',
                          pointerEvents: 'none'
                        }}
                      />
                    )}
                  </div>
                  <button
                    style={{
                      marginLeft: '8px',
                      height: '50px',
                      lineHeight: '40px',
                      padding: '0 12px',
                      fontSize: '12px',
                      cursor: 'pointer',
                      borderRadius: '4px',
                      border: 'none',
                      backgroundColor: '#000',
                      color: '#fff',
                    }}
                    onClick={async () => {
                      //   
                      if (!validateNickname(nickname)) {
                        setNicknameCheckedStatus("invalid");
                        setNicknameCheckMessage(" .");
                        setIsNicknameChecked(false);
                        return;
                      }
                      try {
                        const response = await fetch(`${BASE_URL}/api/auth/check-nickname?nickname=${nickname}`);
                        if (response.ok) {
                          setNicknameCheckedStatus("success");
                          setNicknameCheckMessage("  .");
                          setIsNicknameChecked(true); //     
                        } else {
                          setNicknameCheckedStatus("duplicate");
                          setNicknameCheckMessage(" .");
                          setIsNicknameChecked(false);
                        }
                      } catch (error) {
                        console.error("Nickname check error:", error);
                        setNicknameCheckedStatus("duplicate");
                        setNicknameCheckMessage("  .");
                        setIsNicknameChecked(false);
                      }
                    }}
                  >
                     
                  </button>
                </div>
                {/*    */}
                <div style={{ marginTop: '4px' }}>
                  {nicknameCheckedStatus === "success" ? (
                    //   &    :         ()
                    <p style={{ margin: 0, fontSize: '12px', color: 'green' }}>
                      {nicknameCheckMessage}
                    </p>
                  ) : (
                    <>
                      <p style={{ margin: 0, fontSize: '12px', color: 'red' }}>
                        -  8,  14  
                      </p>
                      <p style={{ margin: 0, fontSize: '12px', color: 'red' }}>
                        - ,  
                      </p>
                      {(nicknameCheckedStatus === "duplicate" || nicknameCheckedStatus === "invalid") && (
                        <p style={{ margin: 0, fontSize: '12px', color: 'red' }}>
                          - {nicknameCheckMessage}
                        </p>
                      )}
                    </>
                  )}
                </div>
              </div>



              {/* Email */}
              <div style={{ marginBottom: '10px' }}>
                <label style={{ display: 'block', marginBottom: '4px' }}>Email address</label>
                <div style={{ display: 'flex' }}>
                  <div style={{ position: 'relative', flex: 1 }}>
                    <input
                      type="email"
                      placeholder=" ."
                      style={{
                        width: '100%',
                        padding: '12px 40px 12px 12px',  //   
                        border: `1px solid ${
                          isEmailChecked
                            ? "green"
                            : (emailValidationMessage && emailValidationColor === "red")
                            ? "red"
                            : "#ccc"
                        }`,
                        borderRadius: '4px',
                        marginBottom: '4px',
                      }}
                      value={signUpEmail}
                      onChange={handleEmailChange}
                    />
                    {(isEmailChecked || (emailValidationMessage && emailValidationColor === "red")) && (
                      <img
                        src={isEmailChecked ? "check_icon.png" : "caution_icon.png"}
                        alt="validation icon"
                        style={{
                          position: 'absolute',
                          right: '10px',
                          top: '50%',
                          transform: 'translateY(-50%)',
                          width: '20px',
                          height: '20px',
                          pointerEvents: 'none'
                        }}
                      />
                    )}
                  </div>
                  <button
                    style={{
                      marginLeft: '8px',
                      height: '50px',
                      lineHeight: '40px',
                      padding: '0 12px',
                      fontSize: '12px',
                      cursor: 'pointer',
                      borderRadius: '4px',
                      border: 'none',
                      backgroundColor: '#000',
                      color: '#fff',
                    }}
                    onClick={async () => {
                      const emailRegex = /^[\w-.]+@([\w-]+\.)+[\w-]{2,}$/;
                      if (!emailRegex.test(signUpEmail)) {
                        setEmailValidationMessage("   .");
                        setEmailValidationColor("red");
                        setIsEmailChecked(false);
                        return;
                      }
                      try {
                        const response = await fetch(`${BASE_URL}/api/auth/check-email?email=${signUpEmail}`);
                        if (response.ok) {
                          setEmailValidationMessage("  .");
                          setEmailValidationColor("green");
                          setIsEmailChecked(true); //   
                        } else {
                          setEmailValidationMessage(" .");
                          setEmailValidationColor("red");
                          setIsEmailChecked(false);
                        }
                      } catch (error) {
                        console.error('Email check error:', error);
                        setIsEmailChecked(false);
                      }
                    }}
                  >
                     
                  </button>
                </div>
                {emailValidationMessage && (
                  <div style={{ color: emailValidationColor, fontSize: '12px' }}>
                    {emailValidationMessage}
                  </div>
                )}
              </div>


              {/* Password */}
              <div style={{ marginBottom: '20px' }}>
                <label style={{ display: 'block', marginBottom: '4px' }}>Password</label>
                <div style={{ position: 'relative' }}>
                  <input
                    type={isSignUpPasswordVisible ? "text" : "password"}
                    placeholder=" ."
                    style={{
                      width: '100%',
                      padding: '12px 40px 12px 12px',
                      border: `1px solid ${signUpPassword ? (isPasswordValid ? "green" : "red") : "#ccc"}`,
                      borderRadius: '4px',
                      marginBottom: '8px',
                    }}
                    value={signUpPassword}
                    onChange={(e) => {
                      const value = e.target.value;
                      setSignUpPassword(value);
                      const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]).{8,16}$/;
                      if (passwordRegex.test(value)) {
                        setPasswordValidationMessage('  .');
                        setPasswordValidationColor('green');
                        setIsPasswordValid(true);
                      } else {
                        setPasswordValidationMessage(' //  8~16 .');
                        setPasswordValidationColor('red');
                        setIsPasswordValid(false);
                      }
                    }}
                  />
                  <img
                    src={isSignUpPasswordVisible ? "show_icon.png" : "hide_icon.png"}
                    alt="toggle password"
                    style={{
                      position: 'absolute',
                      right: '10px',
                      top: '45%',
                      transform: 'translateY(-50%)',
                      width: '20px',
                      height: '20px',
                      cursor: 'pointer'
                    }}
                    onClick={() => setIsSignUpPasswordVisible(!isSignUpPasswordVisible)}
                  />
                </div>
                <div style={{ color: passwordValidationColor, fontSize: '12px', marginBottom: '20px' }}>
                  {passwordValidationMessage}
                </div>
              </div>



              {/* Sign up  */}
              <button
                style={{
                  width: '100%',
                  backgroundColor: isSignUpDisabled ? '#ddd' : '#000',
                  color: isSignUpDisabled ? '#000' : '#fff',
                  border: 'none',
                  borderRadius: '20px',
                  padding: '12px',
                  cursor: 'pointer',
                }}
                disabled={isSignUpDisabled}
                onClick={handleSignUp}
              >
                Sign up
              </button>
            </>
          )}

          
          {/*   */}
          {activeTab === 'login' && (
            <>
              <h2
                style={{
                  textAlign: 'center',
                  marginBottom: '40px',
                  fontSize: '28px',
                  fontWeight: 'bold',
                }}
              >
                Log in
              </h2>

              {/* Email */}
              <div style={{ marginBottom: '10px' }}>
                <label style={{ display: 'block', marginBottom: '4px' }}>
                  Email address
                </label>
                <input
                  type="email"
                  placeholder=' .'
                  style={{
                    width: '100%',
                    padding: '12px',
                    border: '1px solid #ccc',
                    borderRadius: '4px',
                    marginBottom: '20px',
                  }}
                  value={loginEmail}
                  onChange={(e) => {
                    setLoginEmail(e.target.value);
                    setLoginErrorMessage(''); //      
                  }}
                />
            </div>

              {/* Password */}
              <div style={{ marginBottom: '20px' }}>
                <label style={{ display: 'block', marginBottom: '4px' }}>Password</label>
                <div style={{ position: 'relative' }}>
                  <input
                    type={isLoginPasswordVisible ? "text" : "password"}
                    placeholder=" ."
                    style={{
                      width: '100%',
                      padding: '12px 40px 12px 12px',
                      border: '1px solid #ccc',
                      borderRadius: '4px',
                      marginBottom: '8px',
                    }}
                    value={loginPassword}
                    onChange={(e) => {
                      setLoginPassword(e.target.value);
                      setLoginErrorMessage('');
                    }}
                  />
                  <img
                    src={isLoginPasswordVisible ? "show_icon.png" : "hide_icon.png"}
                    alt="toggle password"
                    style={{
                      position: 'absolute',
                      right: '10px',
                      top: '45%',
                      transform: 'translateY(-50%)',
                      width: '20px',
                      height: '20px',
                      cursor: 'pointer'
                    }}
                    onClick={() => setIsLoginPasswordVisible(!isLoginPasswordVisible)}
                  />
                </div>
                {loginErrorMessage && (
                  <div style={{ color: 'red', fontSize: '12px', marginTop: '4px' }}>
                    {loginErrorMessage}
                  </div>
                )}
              </div>


              {/*       */}
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  marginBottom: '20px',
                }}
              >
                <label style={{ display: 'flex', alignItems: 'center', fontSize: '14px' }}>
                  <input
                    type="checkbox"
                    checked={rememberMe}
                    onChange={(e) => setRememberMe(e.target.checked)}
                    style={{ marginRight: '5px' }}
                  />
                   
                </label>
                <span
                  style={{
                    color: '#0000EE',
                    textDecoration: 'underline',
                    cursor: 'pointer',
                    fontSize: '14px',
                  }}
                  onClick={() => setShowForgotPasswordModal(true)}
                >
                   
                </span>
              </div>

              <button
                style={{
                  width: '100%',
                  backgroundColor: isLoginDisabled ? '#ddd' : '#000',
                  color: isLoginDisabled ? '#000' : '#fff',
                  border: 'none',
                  borderRadius: '20px',
                  padding: '12px',
                  cursor: 'pointer',
                }}
                disabled={isLoginDisabled}
                onClick={handleLogin}
              >
                Log in
              </button>
            </>
          )}

        </div>


        {/*    */}
        {showForgotPasswordModal && (
          <div style={{
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(0,0,0,0.5)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            zIndex: 9999,
          }}>
            <div style={{
              backgroundColor: '#fff',
              borderRadius: '10px',
              padding: '40px',
              width: '400px',
              position: 'relative',
            }}>
              <div style={{
                position: 'absolute',
                top: '1px',
                right: '10px',
                cursor: 'pointer',
                fontSize: '24px',
              }} onClick={() => setShowForgotPasswordModal(false)}>
                &times;
              </div>
              <h2 style={{ textAlign: 'center', marginBottom: '25px', fontSize: '20px', fontWeight: 'bold' }}> </h2>
              <div style={{ marginBottom: '10px' }}>
                <label style={{ display: 'block', marginBottom: '4px' }}>Email address</label>
                <input
                  type="email"
                  value={forgotEmail}
                  placeholder=' .'
                  onChange={(e) => { setForgotEmail(e.target.value); setForgotEmailError(''); }}
                  style={{ width: '100%', padding: '12px', border: '1px solid #ccc', borderRadius: '4px' }}
                />
                {forgotEmailError && (
                  <div style={{ color: 'red', fontSize: '12px', marginTop: '4px' }}>{forgotEmailError}</div>
                )}
              </div>
              <button
                style={{
                  width: '100%',
                  backgroundColor: '#000',
                  color: '#fff',
                  border: 'none',
                  borderRadius: '20px',
                  padding: '12px',
                  cursor: 'pointer',
                  marginTop: '10px'
                }}
                onClick={async () => {
                  try {
                    const response = await fetch(`${BASE_URL}/api/auth/forgot-password`, {
                      method: 'POST',
                      headers: { 'Content-Type': 'application/json' },
                      body: JSON.stringify({ email: forgotEmail }),
                    });
                    if (response.ok) {
                      setShowForgotPasswordModal(false);
                      setShowResetPasswordModal(true);
                    } else {
                      const errorText = await response.text();
                      if (errorText === "  .") {
                        setForgotEmailError("  ");
                      } else {
                        setForgotEmailError(" ");
                      }
                    }
                  } catch (error) {
                    console.error("Forgot Password Error:", error);
                    setForgotEmailError(" ");
                  }
                }}
              >
                
              </button>
            </div>
          </div>
        )}

        {/*    */}
        {showResetPasswordModal && (
          <div style={{
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(0,0,0,0.5)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            zIndex: 9999,
          }}>
            <div style={{
              backgroundColor: '#fff',
              borderRadius: '10px',
              padding: '40px',
              width: '400px',
              position: 'relative',
            }}>
              <div style={{
                position: 'absolute',
                top: '1px',
                right: '10px',
                cursor: 'pointer',
                fontSize: '24px',
              }} onClick={() => setShowResetPasswordModal(false)}>
                &times;
              </div>
              <h2 style={{ textAlign: 'center', marginBottom: '25px', fontSize: '20px', fontWeight: 'bold' }}> </h2>
              <div style={{ marginBottom: '10px' }}>
                <label style={{ display: 'block', marginBottom: '4px' }}> </label>
                <div style={{ position: 'relative' }}>
                  <input
                    type={isResetPasswordVisible ? "text" : "password"}
                    value={newPassword}
                    onChange={(e) => {
                      const value = e.target.value;
                      setNewPassword(value);
                      const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]).{8,16}$/;
                      if (passwordRegex.test(value)) {
                        setNewPasswordValidationMessage('  .');
                        setNewPasswordValidationColor('green');
                      } else {
                        setNewPasswordValidationMessage(' //  8~16 .');
                        setNewPasswordValidationColor('red');
                      }
                    }}
                    style={{
                      width: '100%',
                      padding: '12px 40px 12px 12px',
                      border: `1px solid ${newPassword ? (newPasswordValidationColor === 'green' ? "green" : "red") : "#ccc"}`,
                      borderRadius: '4px'
                    }}
                  />
                  <img
                    src={isResetPasswordVisible ? "show_icon.png" : "hide_icon.png"}
                    alt="toggle password"
                    style={{
                      position: 'absolute',
                      right: '10px',
                      top: '50%',
                      transform: 'translateY(-50%)',
                      width: '20px',
                      height: '20px',
                      cursor: 'pointer'
                    }}
                    onClick={() => setIsResetPasswordVisible(!isResetPasswordVisible)}
                  />
                </div>
                <div style={{ color: newPasswordValidationColor, fontSize: '12px', marginTop: '4px' }}>
                  {newPasswordValidationMessage}
                </div>
              </div>

              <button
                style={{
                  width: '100%',
                  backgroundColor: '#000',
                  color: '#fff',
                  border: 'none',
                  borderRadius: '20px',
                  padding: '12px',
                  cursor: 'pointer',
                  marginTop: '10px'
                }}
                onClick={async () => {
                  try {
                    const response = await fetch(`${BASE_URL}/api/auth/reset-password`, {
                      method: 'POST',
                      headers: { 'Content-Type': 'application/json' },
                      body: JSON.stringify({
                        email: forgotEmail,
                        newPassword: newPassword,
                      }),
                    });
                    if (!response.ok) {
                      alert("  .");
                      return;
                    }
                    const result = await response.text();
                    alert(result);
                    setShowResetPasswordModal(false);
                  } catch (error) {
                    console.error("Reset Password Error:", error);
                    alert("    .");
                  }
                }}
              >
                
              </button>
            </div>
          </div>
        )}



        {/*    */}
        {signUpSuccess && (
          <div
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              width: '100%',
              height: '100%',
              backgroundColor: 'rgba(0,0,0,0.5)',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              zIndex: 9999,
            }}
          >
            <div
              style={{
                backgroundColor: '#fff',
                borderRadius: '10px',
                padding: '40px',
                textAlign: 'center',
                position: 'relative',
                width: '400px',
              }}
            >
              {/*   */}
              <div
                style={{
                  position: 'absolute',
                  top: '1px',
                  right: '10px',
                  cursor: 'pointer',
                  fontSize: '24px',
                }}
                onClick={() => setSignUpSuccess(false)}
              >
                &times;
              </div>
              <img
                src="/success_icon.png"
                alt="success"
                style={{
                  width: '100px',
                  marginBottom: '30px',
                  display: 'block',
                  marginLeft: 'auto',
                  marginRight: 'auto',
                }}
              />
              <h2
                style={{
                  fontSize: '18px',
                  marginBottom: '30px',
                  fontWeight: 'bold',
                }}
              >
                {successMessage || "  !"}
              </h2>
              <button
                style={{
                  width: '100%',
                  backgroundColor: '#000',
                  color: '#fff',
                  border: 'none',
                  borderRadius: '20px',
                  padding: '10px',
                  cursor: 'pointer',
                }}
                onClick={() => {
                  setSignUpSuccess(false);
                  setActiveTab('login');
                }}
              >
                Log in
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default AuthModal;
