import React, { useState, useEffect, useMemo } from 'react';
import AnnualLineChart from '../components/AnnualLineChart';
import MonthlyBarChart from '../components/MonthlyBarChart';
import GenreDoughnutChart from '../components/GenreDoughnutChart';
const BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8083';

interface Post {
  id: number;
  readingStatus: '' | '';
  endDate?: string; //   (ISO )
  genre?: string;
}

// /progress API   ()
interface ProgressData {
  yearlyAchieved: number;
  monthlyAchieved: number;
  yearlyData: { month: string; count: number }[];
  recent6Months: { month: string; count: number }[];
  genreData: { label: string; value: number }[];
}

interface GenreData {
  label: string;
  value: number; //  
  count: number;       //  
}

const ProgressPage: React.FC = () => {
  //   ( /posts API )
  const [posts, setPosts] = useState<Post[]>([]);
  // Progress   ( /progress API )
  const [progressData, setProgressData] = useState<ProgressData | null>(null);

  //     (, )
  const [yearlyGoal, setYearlyGoal] = useState(0);
  const [yearlyAchieved, setYearlyAchieved] = useState(0);
  const [monthlyGoal, setMonthlyGoal] = useState(0);
  const [monthlyAchieved, setMonthlyAchieved] = useState(0);

  //    ( )
  const [lineChartData, setLineChartData] = useState<{ month: string; count: number }[]>([]);
  const [barChartData, setBarChartData] = useState<{ month: string; goal: number; achieved: number }[]>([]);
  const [genreData, setGenreData] = useState<GenreData[]>([]);

  // Modal  
  const [showYearlyGoalModal, setShowYearlyGoalModal] = useState(false);
  const [showMonthlyGoalModal, setShowMonthlyGoalModal] = useState(false);
  const [tempGoalValue, setTempGoalValue] = useState('');
  const [tempMonthlyGoalValue, setTempMonthlyGoalValue] = useState('');

  //  , ,  useMemo    
  const currentDate = useMemo(() => new Date(), []); 
  const currentYear = useMemo(() => currentDate.getFullYear(), [currentDate]);
  const currentMonth = useMemo(() => currentDate.getMonth(), [currentDate]);

  //   (  )
  const [selectedYear, setSelectedYear] = useState(currentYear);
  const registrationYear = Number(localStorage.getItem('registrationYear')) || currentYear;

  const yearlyRatio = yearlyGoal > 0 ? (yearlyAchieved / yearlyGoal) : 0;
  const yearlyAngle = yearlyRatio * 360;
  const monthlyRatio = monthlyGoal > 0 ? (monthlyAchieved / monthlyGoal) : 0;
  const monthlyAngle = monthlyRatio * 360;

  const [selectedMonth, setSelectedMonth] = useState(currentMonth + 1);
  const [monthlyGoalVersion, setMonthlyGoalVersion] = useState(0);

  const [selectedGoalYear, setSelectedGoalYear] = useState(currentYear);

  const colorPalette = [
    '#FF6384', // 
    '#36A2EB', // 
    '#FFCE56', // 
    '#62AADF', // 
    '#E6EEF5', // 
    '#b38feb', // 
  ];

  // 
  // (A) /posts API     
  // 
  useEffect(() => {
    fetch(`${BASE_URL}/posts`)
      .then((res) => {
        if (!res.ok) throw new Error('  ');
        return res.json();
      })
      .then((data: Post[]) => setPosts(data))
      .catch((err) => console.error(err));
  }, []);

  useEffect(() => {
    const currentEmail = localStorage.getItem('email');
    const goalEmail = localStorage.getItem('goalEmail');
    const currentYear = new Date().getFullYear();
  
    //       , /   
    if (!goalEmail || goalEmail !== currentEmail) {
      localStorage.removeItem(`yearlyGoal_${currentYear}`);
      for (let m = 1; m <= 12; m++) {
        localStorage.removeItem(`monthlyGoal_${currentYear}_${m}`);
      }
      localStorage.setItem('goalEmail', currentEmail || '');
    }
  }, []);
  

  // 
  // (B) /progress API      (setProgressData )
  // 
  useEffect(() => {
    console.log("Fetching progress data for current year:", currentYear);
    fetch(`${BASE_URL}/progress?year=${currentYear}`)
      .then((res) => {
        if (!res.ok) throw new Error('Progress   ');
        return res.json();
      })
      .then((data: ProgressData) => {
        setProgressData(data);
      })
      .catch(console.error);
  }, [currentYear]);

  // ''  
  const finishedPosts = useMemo(() => 
    posts.filter(post => post.readingStatus === ''),
    [posts]
  );

  
  //    ( )
  useEffect(() => {
    const storedYearlyGoal = localStorage.getItem(`yearlyGoal_${selectedGoalYear}`);
    if (storedYearlyGoal) {
      setYearlyGoal(Number(storedYearlyGoal));
    } else {
      setYearlyGoal(0);
    }
  }, [selectedGoalYear]);

  //    (   )
  useEffect(() => {
    const storedMonthlyGoal = localStorage.getItem(`monthlyGoal_${selectedGoalYear}_${selectedMonth}`);
    if (storedMonthlyGoal) {
      setMonthlyGoal(Number(storedMonthlyGoal));
    } else {
      setMonthlyGoal(0);
    }
  }, [selectedGoalYear, selectedMonth]);



  // 
  // (C)     (  )
  // 
  useEffect(() => {
    const finishedYearlyPosts = posts.filter((post) => {
      if (post.readingStatus !== '' || !post.endDate) return false;
      const d = new Date(post.endDate);
      //   ,       
      return d.getFullYear() === selectedGoalYear && d.getTime() <= Date.now();
    });
    setYearlyAchieved(finishedYearlyPosts.length);
  }, [posts, selectedGoalYear]);
  

  // 
  // (D)     ( ,   )
  // 
  useEffect(() => {
    const monthFinished = posts.filter((post) => {
      if (post.readingStatus !== '' || !post.endDate) return false;
      const d = new Date(post.endDate);
      return (d.getFullYear() === selectedGoalYear && d.getMonth() + 1 === selectedMonth);
    });
    setMonthlyAchieved(monthFinished.length);
  }, [posts, selectedGoalYear, selectedMonth]);
  

  // 
  // (E)     ( 6)
  useEffect(() => {
    const tempData: { month: string; count: number }[] = [];
    let startYear: number, endYear: number;
    // registrationYear   ,  ProgressPage   .
    if (registrationYear === currentYear) {
      //     , currentYear currentYear+5 
      startYear = currentYear;
      endYear = currentYear + 5;
    } else {
      //  ,     
      startYear = registrationYear;
      endYear = currentYear;
    }
    
    for (let y = startYear; y <= endYear; y++) {
      const count = finishedPosts.filter((p) => {
        if (!p.endDate) return false;
        const d = new Date(p.endDate);
        //  targetYear         (  )
        return d.getFullYear() === y;
      }).length;
      tempData.push({ month: `${y}`, count });
    }
    setLineChartData(tempData);
  }, [finishedPosts, registrationYear, currentYear]);
  

  // 
  // (F)     ( 6)
  // 
  useEffect(() => {
    const tempData: { month: string; goal: number; achieved: number }[] = [];
    for (let m = 1; m <= 12; m++) {
      const achievedCount = finishedPosts.filter((p) => {
        if (!p.endDate) return false;
        const d = new Date(p.endDate);
        return d.getFullYear() === selectedGoalYear && d.getMonth() + 1 === m;
      }).length;
      const goalKey = `monthlyGoal_${selectedGoalYear}_${m}`;
      const storedGoal = localStorage.getItem(goalKey);
      const goal = storedGoal ? Number(storedGoal) : 0;
      tempData.push({
        month: `${m}`,
        goal,
        achieved: achievedCount,
      });
    }
    setBarChartData(tempData);
  }, [finishedPosts, monthlyGoalVersion, selectedGoalYear]);
  

  // 
  // (G)     
  // 
  useEffect(() => {
    const postsWithGenre = posts.filter((p): p is Post & { genre: string } =>
      Boolean(p.genre && p.genre.trim() !== '')
    );
    const total = postsWithGenre.length;
    const genreCount: Record<string, number> = {};
    postsWithGenre.forEach((p) => {
      const g = p.genre;
      genreCount[g] = (genreCount[g] || 0) + 1;
    });
    const tempData: GenreData[] = Object.keys(genreCount).map((genre) => ({
      label: genre,
      value: total > 0 ? (genreCount[genre] / total) * 100 : 0,
      count: genreCount[genre],
    }));
    setGenreData(tempData);
  }, [posts]);

  //      
  const handleYearlyGoalSubmit = () => {
    const newGoal = parseInt(tempGoalValue, 10) || 0;
    if (newGoal <= -1) {
      alert('1   .');
      return;
    }
    setYearlyGoal(newGoal);
    localStorage.setItem(`yearlyGoal_${selectedGoalYear}`, String(newGoal));
    //      HomePage  'yearlyGoal'  
    if (selectedGoalYear === currentYear) {
      localStorage.setItem("yearlyGoal", String(newGoal));
    }
    setShowYearlyGoalModal(false);
    setTempGoalValue('');
  };
  

  //    'OK' 
  const handleMonthlyGoalSubmit = () => {
        const newGoal = parseInt(tempMonthlyGoalValue, 10) || 0;
        localStorage.setItem(`monthlyGoal_${selectedGoalYear}_${selectedMonth}`, String(newGoal));
        setMonthlyGoal(newGoal);
        setShowMonthlyGoalModal(false);
        setTempMonthlyGoalValue('');
        setMonthlyGoalVersion(prev => prev + 1); //     
      };

  return (
    <div
      style={{
        minWidth: '1300px',
        backgroundColor: '#f5f5f5',
        minHeight: '100vh',
        paddingTop: '80px',  //  (65px)  
        margin: 0,
      }}
    >
      {/* 2)       */}
      <div style={{ width: '100%',  margin: '0 auto' }}>
        {/* 3)    */}
        <div
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            gap: '20px',
            padding: '0 120px', //   (  )
          }}
        >
      {/* Left: Goal Management */}
      <div
        style={{
          flex: '0 0 400px',      //  maxWidth: 400px  500px
          boxSizing: 'border-box',
          marginTop: '40px',
        }}
      >
        {/*  :       ( width 100%) */}
        <div
          style={{
            backgroundColor: '#D8D1B9',
            borderRadius: '10px',
            boxShadow: '0 0 8px rgba(0,0,0,0.1)',
            border: '1px solid #DADADA',
            padding: '10px 30px',
            marginBottom: '20px',
            textAlign: 'center',
            color: '#333',
            width: '100%',
          }}
        >
          <h2 style={{ fontSize: '24px', fontWeight: 'bold', margin: 0}}>Goal Management</h2>
        </div>

        {/* Yearly Goal Card */}
        <div
          style={{
            backgroundColor: '#fff',
            padding: '25px',
            margin: '20px 0 50px 0',
            boxShadow: '0 0 8px rgba(0,0,0,0.1)',
            border: '1px solid #DADADA',
            minHeight: '480px',
            width: '100%',
          }}
        >
          <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: '10px' }}>
            <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
              <h3 style={{ fontSize: '20px', fontWeight: 'bold', margin: 0 }}>{selectedGoalYear} </h3>
              <span style={{ fontSize: '13px', color: '#555', marginTop: '6px' }}>Annual Goal</span>
            </div>
            <select
              value={selectedGoalYear}
              onChange={(e) => setSelectedGoalYear(Number(e.target.value))}
              style={{ padding: '5px 10px' }}
            >
              {Array.from({ length: 11 }, (_, i) => currentYear + 5 - i).map((year) => (
                <option key={year} value={year}>{year}</option>
              ))}
            </select>
          </div>
          <hr style={{ border: '0.5px solid #ccc', marginBottom: '30px' }} />
          
          
          {/* Donut Graph for Yearly Goal */}
          <div
            style={{
              width: '170px',   //  
              height: '170px',
              borderRadius: '50%',
              margin: '0 auto 40px',
              position: 'relative',
              background: `conic-gradient(
                #FF5C00 0deg ${yearlyAngle}deg,   /*  ( ) */
                #F0F0F0 ${yearlyAngle}deg 360deg  /*  ( ) */
              )`,
            }}
          >
            {/*   (  ''  ) */}
            <div
              style={{
                width: '150px',
                height: '150px',
                backgroundColor: '#fff',
                borderRadius: '50%',
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
              }}
            />

            {/*   (,  ) */}
            <div
              style={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                textAlign: 'center',
              }}
            >
              <span style={{ fontSize: '18px', fontWeight: 'bold' }}>
                {yearlyGoal === 0
                  ? '0%'
                  : yearlyAchieved >= yearlyGoal
                  ? '100%'
                  : `${Math.round(yearlyRatio * 100)}%`}
              </span>
              <div style={{ fontSize: '13px', color: '#666', marginTop: '5px' }}>
                {yearlyGoal === 0
                  ? <><br />.</>
                  : yearlyAchieved === 0
                  ? <> <br /> .</>
                  : yearlyAchieved < yearlyGoal
                  ? `  ${100 - Math.round(yearlyRatio * 100)}% .`
                  : ' !'}
              </div>
            </div>
          </div>

          {/* Yearly Goal Modal Trigger */}
          <div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: '20px' }}>
            <button
               onClick={() => {
                setTempGoalValue(String(yearlyGoal));
                setShowYearlyGoalModal(true);
              }}
              style={{
                backgroundColor: '#666',
                color: '#fff',
                border: 'none',
                borderRadius: '20px',
                padding: '8px 20px',
                cursor: 'pointer',
                fontSize: '14px',
              }}
            >
               
            </button>
          </div>

          {/* Yearly Goal / Achieved Display */}
          <div
            style={{
              backgroundColor: '#FEF9F3',
              borderRadius: '8px',
              padding: '14px 18px',
            }}
          >
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '8px' }}>
              <div style={{ fontWeight: 'bold', fontSize: '14px' }}></div>
              <div style={{ fontSize: '14px' }}>{yearlyGoal}</div>
            </div>
            <hr style={{ border: '0.1px solid #C5BBB1', margin: '8px 0' }} />
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '8px' }}>
              <div style={{ fontWeight: 'bold', fontSize: '14px' }}></div>
              <div style={{ fontSize: '14px' }}>{yearlyAchieved}</div>
            </div>
          </div>
        </div>

        {/* Monthly Goal Card */}
        <div
          style={{
            backgroundColor: '#fff',
            
            padding: '25px',
            marginBottom: '30px',
            boxShadow: '0 0 8px rgba(0,0,0,0.1)',
            border: '1px solid #DADADA',
            /*     minHeight  (: 350px) */
            minHeight: '480px',
            width: '100%',
          }}
        >
          <div style={{ display: 'flex', alignItems: 'baseline', justifyContent: 'space-between', marginBottom: '10px' }}>
            <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
            <h3 style={{ fontSize: '20px', fontWeight: 'bold', margin: 0 }}>
              {selectedMonth.toString().padStart(2, '0')} 
            </h3>
              <span style={{ fontSize: '13px', color: '#555', marginTop: '6px' }}>{selectedGoalYear} Monthly Goal</span>
            </div>
            <select
              value={selectedMonth}
              onChange={(e) => setSelectedMonth(Number(e.target.value))}
              style={{ padding: '5px 10px' }}
            >
              {Array.from({ length: 12 }, (_, i) => i + 1).map((month) => (
                <option key={month} value={month}>
                  {month.toString().padStart(2, '0')}
                </option>
              ))}
            </select>
          </div>
          <hr style={{ border: '0.5px solid #ccc', marginBottom: '30px' }} />

          {/* Donut Graph for Monthly Goal */}
          <div
            style={{
              width: '170px',
              height: '170px',
              borderRadius: '50%',
              margin: '0 auto 40px',
              position: 'relative',
              background: `conic-gradient(
                #62AADF 0deg ${monthlyAngle}deg,
                #F0F0F0 ${monthlyAngle}deg 360deg
              )`,
            }}
          >
            <div
              style={{
                width: '150px',
                height: '150px',
                backgroundColor: '#fff',
                borderRadius: '50%',
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
              }}
            />
            <div
              style={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                textAlign: 'center',
              }}
            >
              <span style={{ fontSize: '18px', fontWeight: 'bold' }}>
                {monthlyGoal === 0
                  ? '0%'
                  : monthlyAchieved >= monthlyGoal
                  ? '100%'
                  : `${Math.round(monthlyRatio * 100)}%`}
              </span>
              <div style={{ fontSize: '13px', color: '#666', marginTop: '5px' }}>
                {monthlyGoal === 0
                  ? <><br />.</>
                  : monthlyAchieved === 0
                  ? <> <br /> .</>
                  : monthlyAchieved < monthlyGoal
                  ? `  ${100 - Math.round(monthlyRatio * 100)}% .`
                  : ' !'}
              </div>
            </div>
          </div>

          {/* Monthly Goal Modal Trigger */}
          <div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: '20px' }}>
            <button
              onClick={() => {
                setTempMonthlyGoalValue(String(monthlyGoal));
                setShowMonthlyGoalModal(true);
              }}
              style={{
                backgroundColor: '#666',
                color: '#fff',
                border: 'none',
                borderRadius: '20px',
                padding: '8px 20px',
                cursor: 'pointer',
                fontSize: '14px',
                
              }}
            >
               
            </button>
          </div>

          {/* Monthly Goal / Achieved Display */}
          <div
            style={{
              backgroundColor: '#FEF9F3',
              borderRadius: '8px',
              padding: '14px 18px',
            }}
          >
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '8px' }}>
              <div style={{ fontWeight: 'bold', fontSize: '14px' }}></div>
              <div style={{ fontSize: '14px' }}>{monthlyGoal}</div>
            </div>
            <hr style={{ border: '0.1px solid #C5BBB1', margin: '8px 0' }} />
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '8px' }}>
              <div style={{ fontWeight: 'bold', fontSize: '14px' }}></div>
              <div style={{ fontSize: '14px' }}>{monthlyAchieved}</div>
            </div>
          </div>
        </div>
      </div>

      {/* Right: Statistics */}
      <div
        style={{
          flex: '2 1 50%',
          minWidth: '300px',
          boxSizing: 'border-box',
          paddingTop: '40px',
          marginLeft: '40px'
        }}
      >
        {/*   */}
        <div
          style={{
            backgroundColor: '#C5CAB4',
            borderRadius: '10px',
            boxShadow: '0 0 8px rgba(0,0,0,0.1)',
            border: '1px solid #DADADA',
            padding: '10px 40px',
            marginBottom: '20px',
            textAlign: 'center',
            color: '#333',
            width: '100%',
          }}
        >
          <h2 style={{ fontSize: '24px', fontWeight: 'bold', margin: 0}}>Statistics</h2>
        </div>

        {/* Annual Reading Amount */}
        <div style={{ marginBottom: '30px' }}>
          <h4 style={{ fontSize: '18px', margin: '20px 0 10px 0', fontWeight: 'bold' }}> </h4>
          
          {/*      */}
          <div
            style={{
              backgroundColor: '#fff',
              padding: '20px 40px',
              boxShadow: '0 0 8px rgba(0,0,0,0.1)',
              border: '1px solid #DADADA',
              marginBottom: '30px',
            }}
          >
            {/*    */}
            <div
              style={{
                display: 'flex',
                justifyContent: 'flex-end',
                alignItems: 'center',
                marginBottom: '10px',
              }}
            >
            </div>
            <AnnualLineChart
              chartData={lineChartData}
              registrationYear={registrationYear}
              fromLabel={` : ${registrationYear} ~ `}
            />
        </div>
      </div>

        {/* Monthly Reading Amount */}
        <div style={{ marginBottom: '30px',  }}>
          <h4 style={{ fontSize: '18px', margin: '0 0 10px 0', fontWeight: 'bold' }}> </h4>
          {
            (() => {
              const currentYearLocal = new Date().getFullYear();
              let zoomMin: number, zoomMax: number;
              if (selectedGoalYear === currentYearLocal) {
                const curMonthIndex = new Date().getMonth(); // 0-based index
                //  :       (:   4  1~6, 5 2~7)
                zoomMin = curMonthIndex - 3;
                zoomMax = zoomMin + 5;
                if (zoomMin < 0) {
                  zoomMin = 0;
                  zoomMax = 5;
                }
                if (zoomMax > 11) {
                  zoomMax = 11;
                  zoomMin = 11 - 5;
                }
              } else {
                zoomMin = 0;
                zoomMax = 5;
              }
              return (
                <MonthlyBarChart
                  chartData={barChartData}
                  monthlyGoal={monthlyGoal}
                  defaultMin={zoomMin}
                  defaultMax={zoomMax}
                  selectedYear={selectedGoalYear}  // :    
                />
              );
            })()
          }
        </div>

        {/* Genre Reading Ratio */}
        <h4 style={{ fontSize: '18px', margin: '0 0 10px 0', fontWeight: 'bold' }}>  </h4>
        <div
          style={{
            backgroundColor: '#fff',
            padding: '20px 40px',
            boxShadow: '0 0 8px rgba(0,0,0,0.1)',
            marginBottom: '40px',
            border: '1px solid #DADADA',
          }}
        >
          {/*     (  ) */}
          <div style={{
            display: 'flex',
            alignItems: 'flex-start',
            gap: '50px', //    
            flexWrap: 'nowrap', //   
            justifyContent: 'flex-start',
          }}>
            {/* :   */}
            <GenreDoughnutChart genreData={genreData} />
            {/* :  */}
            <div style={{ flex: 1 }}>
              <table style={{ width: '100%', borderCollapse: 'collapse' }}>
                <thead>
                  <tr style={{ textAlign: 'left', borderBottom: '1px solid #ddd' }}>
                    <th style={{ padding: '8px 0', fontSize: '14px', fontWeight: 'bold' }}></th>
                    <th style={{ padding: '8px 0', fontSize: '14px', fontWeight: 'bold' }}>Value</th>
                    <th style={{ padding: '8px 0', fontSize: '14px', fontWeight: 'bold' }}>%</th>
                  </tr>
                </thead>
                <tbody>
                  {genreData.length === 0 ? (
                    <tr>
                      <td colSpan={3} style={{ textAlign: 'center', padding: '40px', color: '#999' }}>
                          .
                      </td>
                    </tr>
                  ) : (
                    genreData.map((item, idx) => {
                      const colorDot = colorPalette[idx % colorPalette.length];
                      return (
                        <tr key={idx} style={{ borderBottom: '1px solid #f0f0f0' }}>
                          <td style={{ padding: '8px 0', fontSize: '13px', color: '#333' }}>
                            {/*     */}
                            <span
                              style={{
                                display: 'inline-block',
                                width: '10px',
                                height: '10px',
                                borderRadius: '50%',
                                backgroundColor: colorDot,
                                marginRight: '6px',
                              }}
                            />
                            {item.label}
                          </td>
                          {/* Value(: 1)      '-'   */}
                          <td style={{ padding: '8px 0', fontSize: '13px', color: '#333' }}>
                            {item.count}
                          </td>
                          <td style={{ padding: '8px 0', fontSize: '13px', color: '#333' }}>
                            {item.value.toFixed(1)}%
                          </td>
                        </tr>
                      );
                    })
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
      {/* Yearly Goal Modal */}
      {showYearlyGoalModal && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0,0,0,0.5)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            zIndex: 9999,
          }}
        >
          <div
            style={{
              width: '350px',
              backgroundColor: '#fff',
              borderRadius: '8px',
              padding: '20px',
              textAlign: 'center',
              position: 'relative',
            }}
          >
            <div
              style={{
                position: 'absolute',
                top: '10px',
                right: '15px',
                cursor: 'pointer',
                fontSize: '20px',
              }}
              onClick={() => setShowYearlyGoalModal(false)}
            >
              &times;
            </div>

            <p style={{ fontSize: '16px', fontWeight: 'bold', marginBottom: '20px' }}>
                 .
            </p>

            <input
              type="number"
              value={tempGoalValue}
              onChange={(e) => setTempGoalValue(e.target.value)}
              placeholder="Value"
              style={{
                width: '100%',
                padding: '10px',
                fontSize: '15px',
                marginBottom: '20px',
                border: '1px solid #ccc',
                borderRadius: '4px',
                textAlign: 'center',
              }}
            />

            <div style={{ display: 'flex', justifyContent: 'center', gap: '10px' }}>
              <button
                style={{
                  flex: 1,
                  padding: '10px',
                  backgroundColor: '#fff',
                  border: '1px solid #ccc',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '14px',
                }}
                onClick={() => setShowYearlyGoalModal(false)}
              >
                Cancel
              </button>
              <button
                style={{
                  flex: 1,
                  padding: '10px',
                  backgroundColor: '#000',
                  color: '#fff',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '14px',
                }}
                onClick={handleYearlyGoalSubmit}
              >
                OK
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Monthly Goal Modal */}
      {showMonthlyGoalModal && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0,0,0,0.5)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            zIndex: 9999,
          }}
        >
          <div
            style={{
              width: '350px',
              backgroundColor: '#fff',
              borderRadius: '8px',
              padding: '20px',
              textAlign: 'center',
              position: 'relative',
            }}
          >
            <div
              style={{
                position: 'absolute',
                top: '1px',
                right: '5px',
                cursor: 'pointer',
                fontSize: '20px',
              }}
              onClick={() => setShowMonthlyGoalModal(false)}
            >
              &times;
            </div>

            <p style={{ fontSize: '20px', fontWeight: 'bold', marginBottom: '20px' }}>
                  .
            </p>

            <input
              type="number"
              value={tempMonthlyGoalValue}
              onChange={(e) => setTempMonthlyGoalValue(e.target.value)}
              placeholder="Value"
              style={{
                width: '100%',
                padding: '10px',
                fontSize: '14px',
                marginBottom: '20px',
                border: '1px solid #ccc',
                borderRadius: '4px',
              }}
            />

            <div style={{ display: 'flex', justifyContent: 'center', gap: '10px' }}>
              <button
                style={{
                  flex: 1,
                  padding: '10px',
                  backgroundColor: '#fff',
                  border: '1px solid #ccc',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '14px',
                }}
                onClick={() => setShowMonthlyGoalModal(false)}
              >
                Cancel
              </button>
              <button
                style={{
                  flex: 1,
                  padding: '10px',
                  backgroundColor: '#000',
                  color: '#fff',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '14px',
                }}
                onClick={handleMonthlyGoalSubmit}
              >
                OK
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
    </div>
  );
};

export default ProgressPage;
